* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

@font-face {
  font-family: Formula1;
  src: url(../public/images/Formula1-Regular.otf);
}
/* css del header */
.navbar {
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px 1px red;
  --webkit-box-shadow: 0 8px 6px -3px red;
  border-bottom: solid red !important;
}

.nav-link {
  font-weight: 300 !important;

}

.active {
  border-bottom: 2px solid red;
}

.navbar-brand {
  font-weight: 700;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  letter-spacing: 3px;
}

.navbar-collapse {
  transition: height 0.5s ease;

}

/*pijadas para el menu del header*/
ul li.carrito {
  position: relative;
  display: grid;
  place-items: center;
  width: 120px;
  height: 50px;
}
ul li.cerrar {
  position: relative;
  display: grid;
  place-items: center;
  width: 120px;
  height: 50px;
}

ul li.sesion {
  position: relative;
  display: grid;
  place-items: center;
  width: 120px;
  height: 50px;
}

ul li.registro {
  position: relative;
  display: grid;
  place-items: center;
  width: 120px;
  height: 50px;
}

ul li.futbol {
  position: relative;
  display: grid;
  place-items: center;
  width: 120px;
  height: 50px;
}

ul li.f1 {
  position: relative;
  display: grid;
  place-items: center;
  width: 120px;
  height: 50px;
}

li .base {
  position: relative;
  width: 100%;
  height: 100%;
  filter: drop-shadow(0 0 10px var(--color));
}

li .base,
li .title,
li .icon {
  position: absolute;
  color: #b5b5b5;
  transform: rotateX(0deg) rotateZ(-0deg);
  cursor: pointer;
  transition: 0.5s ease-in-out;
}

li .title {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  transform: rotateY(500deg) skewY(0deg) translateY(-0px);
  opacity: 0;
  pointer-events: none;
}

li .icon {
  display: grid;
  place-items: center;
  font-size: 80px;
  height: 100%;
  pointer-events: none;
}

.base:hover {
  filter: drop-shadow(0 0 10px var(--color));
  animation-play-state: paused;
}

.base:hover+.title+.icon {
  transform: rotateY(0deg) skewY(-0deg) translateY(-20px);
  color: var(--color);
  filter: drop-shadow(0 0 3px var(--color));
  animation: wave 1s ease-in-out infinite;
}

.base:hover+.title {
  color: var(--color);
  transform: rotateY(0deg) skewY(-0deg) translateY(20px);
  text-shadow: 0 0 3px var(--color);
  transition-delay: 0.1s;
  opacity: 1;
  animation: wave 1s ease-in-out infinite;
}


/*css del footer*/

.footer {
  font-family: Formula1;
  color: white;
  padding: 29px;
  background: #000000;
  border-top: solid red !important;
}

.footer a {
  font-size: 9px;
  font-family: Formula1;
  text-decoration: none;
  position: relative;
  color:white;
  letter-spacing: .5px;
  padding: 0 20px;
  cursor: pointer;
}

.footer a::before,
.footer a::after{
  content: "";
  position: absolute;
  height: 3px;
  width: 0;
  background: red;
  transition: .5s;
}
.footer a::after{
  left: 0;
  bottom: -10px;
}

.footer a::before{
  right: 0;
  top: -10px;
}
.footer a:hover::after,
.footer a:hover::before {
  width: 100%;
}

/* css pagina no encontrada*/

.notfound {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.notfound-title {
  font-size: 100px;
  font-weight: 700;
}

.notfound-heading {
  font-weight: normal;
}

.notfound-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

/*Estilos al mostrar los productos*/

.link-productos{
  text-decoration: none !important;
  color: black !important;
}