@font-face {
    font-family: Formula1;
    src: url(../../public/images/Formula1-Regular.otf);
  }
.form-container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 70vh;
    flex-direction: column;
}

.background-image {
    background-image: url("../../public/images/fondo.png");
    background-size: cover;
    background-position: center;
}

.form-container form {
    box-shadow: rgba(255, 0, 0, 0.35) 0px 0px 10px 10px, 0 0 0 2px #ff0000;
    padding: 20px;
    background-color: #000;
    color: #fff;
    border-radius: 10px;
}

.form-container form h4{
    font-family: Formula1;
}

.form-container form .titulo {
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    letter-spacing: 2px;
}

.form-container form input {
    border: none;
    border-bottom: 1px solid #ff0000;
    border-radius: 0;
    background-color: #000;
    color: #fff;
}

.form-container form button {
    width: 100%;
    padding: 10px 40px;
    background-color: #000;
    border: none;
    font-size: 18px;
    position: relative;
    transition: 300ms;
    color: #ff0000;
}

.form-container form button span {
    font-family: Formula1;
    color: #ff0000;
    position: relative;
    transition: 300ms;
    transition-delay: 300ms;
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 200;
}

.form-container form button::before {
    content: '';
    position: absolute;
    width: 0%;
    height: 0%;
    left: 50%;
    right: 50%;
    top: 50%;
    bottom: 50%;
    transition: 300ms;
    transition-delay: 300ms;
    background-color: #ff0000;
    box-shadow: 0 0 10px #ff0000, 0 0 10px #ff0000, 0 0 10px #ff0000;
}

.form-container form button div {
    transition: 300ms;
    position: absolute;
    background-color: #ff0000;
    box-shadow: 0 0 10px #ff0000, 0 0 10px #ff0000, 0 0 10px #ff0000;
}

.form-container form button .top {
    width: 15px;
    height: 2px;
    top: 0;
    left: 0;
    background-color: #ff0000;
}

.form-container form button .bottom {
    width: 15px;
    height: 2px;
    bottom: 0;
    right: 0;
    background-color: #ff0000;
}

.form-container form button .left {
    width: 2px;
    height: 15px;
    top: 0;
    left: 0;
    background-color: #ff0000;
}

.form-container form button .right {
    width: 2px;
    height: 15px;
    bottom: 0;
    right: 0;
    background-color: #ff0000;
}

.form-container form button:hover {
    color: #000;
}

.form-container form button:hover span {
    color: #000;
}

.form-container form button:hover::before {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.form-container form button:hover .top,
.form-container form button:hover .bottom {
    width: 100%;
}

.form-container form button:hover .left,
.form-container form button:hover .right {
    height: 100%;
}

.form-container form .btn.btn-primary:hover {
    background-color: #000;
}
